<script>
import $ from "jquery";
import axios from "axios";
import Map from "./map";
import { mapGetters } from "vuex";

export default {
  name: "DashboardView",
  components: {
    Map,
  },
  methods: {
    setDate() {
      this.return_date_array = this.campaignInfo.campaignDate;
    },
    deleteCampaign() {
      let campaignToDelete = this.currentCampaign;
      let user_id = this.userDetails.user_id;
      //send campaign for archiving
      let formData = new FormData();
      let data_param = JSON.stringify(campaignToDelete);
      formData.append("data", data_param);
      formData.append("user_id", user_id);
      const api = axios.create({
        baseURL: "http://digitad.us-east-2.elasticbeanstalk.com",
      });
      api
        .post(
          "http://digitad.us-east-2.elasticbeanstalk.com/server/deleteCampaign.php",
          formData
        )
        .then((result) => {
          console.log(result.data);
          if (result.data == 1) {
            //campaign delete
            this.modalText.questionText = "Successful!";
            this.modalText.actionText = "Deleted!";
            //pop out of store
            if (campaignToDelete.approved == 0) {
              //a not yet approved campaign
              this.unApprovedCampaigns.pop(campaignToDelete);
            } else if (
              campaignToDelete.approved == 1 &&
              campaignToDelete.paid == 0
            ) {
              //an approved but not paid campaign
              this.not_paid_campaigns.pop(campaignToDelete);
              this.library_campaigns.pop(campaignToDelete);
            } else if (
              campaignToDelete.approved == 1 &&
              campaignToDelete.paid == 1
            ) {
              this.activeCampaigns.pop(campaignToDelete);
              this.library_campaigns.pop(campaignToDelete);
            }
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    createCurrentCampaign(campaign) {
      //current campain to delete
      (this.modalText.headerText = "Delete Campaign"),
        (this.modalText.actionText = "Delete"),
        (this.modalText.questionText =
          "Are you sure you want to delete this campaign!");
      this.currentCampaign = campaign;
      this.deleteModal = true;
    },
    videoData() {
      this.errors = [];
      let video = this.$refs.video;
      let videoDuration = video.duration;
      this.campaignInfo.duration = videoDuration;
      if (videoDuration > 10) {
        this.errors.push({ value: "MAXIMUM VIDEO LENGTH SHOULD BE 20 SECS." });
      }
      handleErrors();
    },
    handleErrors() {
      if (this.errors.length > 0) {
        let initial_error = this.errors[0];
        this.modalText.questionText = initial_error.value;
        this.errorModal = true;
      }
    },
    launch_archive_campaign(campaignX) {
      //check if campaign is not active
      if (this.activeCampaigns.indexOf(campaignX) == -1) {
        let user_id = this.userDetails.user_id;
        //send campaign for archiving
        let formData = new FormData();
        let data_param = JSON.stringify(campaignX);
        formData.append("data", data_param);
        formData.append("user_id", user_id);
        const api = axios.create({
          baseURL: "http://digitad.us-east-2.elasticbeanstalk.com",
        });
        api
          .post(
            "http://digitad.us-east-2.elasticbeanstalk.com/server/lauch_archive_campaign.php",
            formData
          )
          .then((result) => {
            console.log(result.data);
            if (result.data == 1) {
              //reload page
              location.reload();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    loadImage(type, url) {
      if (
        type == "image/jpeg" ||
        type == "image/jpg" ||
        type == "image/png" ||
        type == "image/gif"
      ) {
        return "http://digitad.us-east-2.elasticbeanstalk.com/img/campaign_img/"+url; //require("../assets/campaign_images/"+url);
      } else {
        return "http://digitad.us-east-2.elasticbeanstalk.com/media/"+url; //require("../assets/campaign_videos/"+url);
      }
    },
    createArchive(campaign) {
      //campaign to archive
      let campaignToArchive = campaign;
      //push to archive if not archived
      // console.log(this.archivedCampaigns.indexOf(campaignToArchive));
      if (this.archivedCampaigns.indexOf(campaignToArchive) == -1) {
        let user_id = this.userDetails.user_id;
        //send campaign for archiving
        let formData = new FormData();
        let data_param = JSON.stringify(campaignToArchive);
        formData.append("data", data_param);
        formData.append("user_id", user_id);
        const api = axios.create({
          baseURL: "http://digitad.us-east-2.elasticbeanstalk.com",
        });
        api
          .post(
            "http://digitad.us-east-2.elasticbeanstalk.com/server/createArchive.php",
            formData
          )
          .then((result) => {
            console.log(result.data);
            if (result.data == 1) {
              //push into archive store
              //reload page
              location.reload();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    getImage(url) {
      //console.log(url);
      return "http://digitad.us-east-2.elasticbeanstalk.com/img/campaign_img/" + url;//require("http://digitad.us-east-2.elasticbeanstalk.com/img/campaign_img" + url);
    },
    addCampaign() {
      let file = this.campaignInfo.campaignMedia;
      let user_id = this.userDetails.user_id;
      let userName = this.userDetails.f_name;
      //create form data object
      //this.activeCampaigns.push(this.campaignInfo);
      let campaignOBJ = this.campaignInfo;
      console.log(campaignOBJ);
      let formData = new FormData();
      let data_param = JSON.stringify(this.campaignInfo);
      formData.append("data", data_param);
      formData.append("file", file);
      formData.append("user_id", user_id);
      formData.append("user_name", userName);
      //check if there are no errors
      const params = { crossDomain: true, withCredentials: false }; //http://localhost/digit-ad/dashboard/campaign/submit_campaign.php
      const api = axios.create({
        baseURL: "http://digitad.us-east-2.elasticbeanstalk.com",
      });
      if (this.errors.length == 0) {
        api
          .post(
            "http://digitad.us-east-2.elasticbeanstalk.com/server/submit_campaign.php",
            formData
          )
          .then((result) => {
            console.log(result);
            if (result.data == 1) {
              //push into new campaign
              //reload page
              location.reload();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        this.handleErrors();
      }
    },
    set_price() {
      this.campaignInfo.campaignCost =
        this.campaignInfo.multiplyConstant * this.campaignInfo.numberOfDays;
      //set launched campaign currency
      this.campaignInfo.currency = this.currency;
      //console.log(this.campaignInfo.capaignCost);
    },
    sort_city() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.city == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
    },
    sortState() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.state == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
    },
    previewCampaignMedia() {
      let file = this.campaignInfo.campaignMedia;
      this.file = this.campaignInfo.campaignMedia;
      let fileType = file.type;
      if (
        fileType == "video/mp4" ||
        fileType == "video/webm" ||
        fileType == "video/ogg"
      ) {
        this.preview_campaign_media = true;
        this.previewMedia = URL.createObjectURL(this.file);
      }
    },
    validateFile() {
      this.errors = [];
      let file = this.campaignInfo.campaignMedia;
      //console.log(file);
      let fileSize = (file.size / (1024 * 1024)).toFixed(2);
      let fileType = file.type;
      let f_name = file.name;
      if (fileSize <= 10) {
        if (
          fileType == "video/mp4" ||
          fileType == "video/webm" ||
          fileType == "image/jpeg" ||
          fileType == "image/jpg" ||
          fileType == "image/png" ||
          fileType == "image/gif" ||
          fileType == "video/ogg"
        ) {
          this.campaignInfo.mediaType = fileType;
          this.campaignInfo.file_name = f_name;
          this.campaignInfo.fSize = fileSize;
        } else {
          this.errors.push({
            value: "INVALID FILE TYPE! ONLY :MP4,WEBM,JPG,PNG AND GIF ALLOWED",
          });
        }
      } else {
        this.errors.push({ value: "FILE TOO LARGE, MAX FILESIZE = 10MB" });
      }
      this.handleErrors();
    },
    calculateDays() {
      let campaignDateRange = this.campaignInfo.campaignDate;
      //console.log(campaignDateRange);
      this.return_date_array = campaignDateRange;
      let startDate = new Date(this.campaignInfo.campaignDate[0]);
      let formatedStartDate = startDate.toDateString();
      this.campaignInfo.startDate = formatedStartDate;
      let endDate = new Date(this.campaignInfo.campaignDate[1]);
      let formatedEndDate = endDate.toDateString();
      this.campaignInfo.endDate = formatedEndDate;
      let timeDifference = endDate.getTime() - startDate.getTime();
      let dateDifference = timeDifference / (1000 * 3600 * 24);
      this.campaignInfo.numberOfDays = dateDifference;
      this.set_price();
    },
    reloadMap() {
      this.mapKey = this.mapKey + 1;
      console.log("reloaded:" + this.mapKey);
    },
    reduce_price() {
      let k = 0;
      if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population > 0 &&
        this.currentScreen.population <= 100000
      ) {
        k = k + 1;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 100001 &&
        this.currentScreen.population <= 500000
      ) {
        k = k + 1.1;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 5000001 &&
        this.currentScreen.population <= 1000000
      ) {
        k = 1.15;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 1000001 &&
        this.currentScreen.population <= 5000000
      ) {
        k = k + 1.2;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 5000001 &&
        this.currentScreen.population <= 10000000
      ) {
        k = k + 1.25;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 10000001 &&
        this.currentScreen.population <= 20000000
      ) {
        k = k + 1.3;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 20000001
      ) {
        k = k + 1.35;
        /////gdp break
      } else if (
        this.currentScreen.gdp >= 1001 &&
        this.currentScreen.gdp <= 3000 &&
        this.currentScreen.population > 0 &&
        this.currentScreen.population <= 100000
      ) {
        k = k + 1.1;
      } else if (
        this.currentScreen.gdp >= 1001 &&
        this.currentScreen.gdp <= 3000 &&
        this.currentScreen.population >= 100001 &&
        this.currentScreen.population <= 500000
      ) {
        k = k + 1.15;
      } else if (
        this.currentScreen.gdp >= 1001 &&
        this.currentScreen.gdp <= 3000 &&
        this.currentScreen.population >= 5000001 &&
        this.currentScreen.population <= 1000000
      ) {
        k = k + 1.2;
      } else if (
        this.currentScreen.gdp >= 1001 &&
        this.currentScreen.gdp <= 3000 &&
        this.currentScreen.population >= 1000001 &&
        this.currentScreen.population <= 5000000
      ) {
        k = k + 1.25;
      } else if (
        this.currentScreen.gdp >= 1001 &&
        this.currentScreen.gdp <= 3000 &&
        this.currentScreen.population >= 5000001 &&
        this.currentScreen.population <= 10000000
      ) {
        k = k + 1.3;
      } else if (
        this.currentScreen.gdp >= 1001 &&
        this.currentScreen.gdp <= 3000 &&
        this.currentScreen.population >= 10000001 &&
        this.currentScreen.population <= 20000000
      ) {
        k = k + 1.35;
      } else if (
        this.currentScreen.gdp >= 1001 &&
        this.currentScreen.gdp <= 3000 &&
        this.currentScreen.population >= 20000001
      ) {
        k = k + 1.4;
        //gdp break
      } else if (
        this.currentScreen.gdp >= 3001 &&
        this.currentScreen.gdp <= 6000 &&
        this.currentScreen.population > 0 &&
        this.currentScreen.population <= 100000
      ) {
        k = k + 1.15;
      } else if (
        this.currentScreen.gdp >= 3001 &&
        this.currentScreen.gdp <= 6000 &&
        this.currentScreen.population >= 100001 &&
        this.currentScreen.population <= 500000
      ) {
        k = k + 1.12;
      } else if (
        this.currentScreen.gdp >= 3001 &&
        this.currentScreen.gdp <= 6000 &&
        this.currentScreen.population >= 5000001 &&
        this.currentScreen.population < 1000000
      ) {
        k = k + 1.25;
      } else if (
        this.currentScreen.gdp >= 3001 &&
        this.currentScreen.gdp <= 6000 &&
        this.currentScreen.population >= 1000001 &&
        this.currentScreen.population <= 5000000
      ) {
        k = k + 1.3;
      } else if (
        this.currentScreen.gdp >= 3001 &&
        this.currentScreen.gdp <= 6000 &&
        this.currentScreen.population >= 5000001 &&
        this.currentScreen.population <= 10000000
      ) {
        k = k + 1.35;
      } else if (
